import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { AuthLayoutComponent } from './auth-layout/auth-layout.component';
import { RoleMatrixComponent } from './role-matrix/role-matrix.component';
import { ListRolesComponent } from './list-roles/list-roles.component';
import { AddRoleComponent } from './add-role/add-role.component';
import { EditRoleComponent } from './edit-role/edit-role.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { ForgotPasswordOtpVerificationComponent } from './forgot-password-otp-verification/forgot-password-otp-verification.component';
import { ForgotPasswordNewPwdConfirmationComponent } from './forgot-password-new-pwd-confirmation/forgot-password-new-pwd-confirmation.component';
import { ForgotPasswordEmailValidationComponent } from './forgot-password-email-validation/forgot-password-email-validation.component';
const routes: Routes = [{
  path: '', component: AuthLayoutComponent,
  children: [
    { path: '', redirectTo: 'login/:id', pathMatch: 'full' },
    { path: 'login/:id', component: LoginComponent },
    { path: 'roleMatrix/:string/:anyCode', component: RoleMatrixComponent },
    { path: 'roles', component: ListRolesComponent },
    { path: 'addRole', component: AddRoleComponent },
    { path: 'editRole/:anyCode', component: EditRoleComponent },

  ]
},
{ path: 'forgotPassEmailValidation', component: ForgotPasswordEmailValidationComponent },
{ path: 'forgotPass/:emailId', component: ForgotPasswordComponent },
{ path: 'forgotPassOtpVerification/:mode/:source/:role', component: ForgotPasswordOtpVerificationComponent },
{ path: 'forgotPassNewPwdConfirmation/:mode/:source/:role', component: ForgotPasswordNewPwdConfirmationComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AuthRoutingModule { }
