import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global'
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({
  providers:[Beyond],
  selector: 'app-edit-role',
  templateUrl: './edit-role.component.html',
  styleUrls: ['./edit-role.component.scss']
})
export class EditRoleComponent implements OnInit {
  editRole: FormGroup
  roleCode: any;
  viewRoles: any;
  deleteobj:any;
  displayConfirmation:any='none;'
  constructor(
    public beyond:Beyond,
    public router: Router,
    public route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.roleCode = this.route.snapshot.params['anyCode'];
    console.log("roleCode"+JSON.stringify(this.roleCode))
    this.editRole = this.formBuilder.group({
      roleCode: ['', Validators.required],
      roleName: ['', Validators.required],
      status: ['', Validators.required]
    })
    this.viewRole(this.roleCode)
  }


  viewRole(roleCode){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url+'/viewRoles/'+ roleCode, options)
      .subscribe(data => {
        var viewRoleData = data.json();
        this.viewRoles = viewRoleData.response[0];
        this.editRole = this.formBuilder.group({
          roleCode: [this.viewRoles.roleCode, Validators.required],
          roleName: [this.viewRoles.roleDescription, Validators.required],
          status: [this.viewRoles.status, Validators.required]
        })
        console.log("service category Data"+JSON.stringify(this.viewRoles))
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  submit() {
    this.deleteobj = {
      "roleCode":this.viewRoles.roleCode,
      "roleDescription":this.viewRoles.roleDescription,
      "status":this.editRole.value.status
    }
    this.displayConfirmation='block'
   
  }
  onDelete(){
    this.displayConfirmation='none'
    this.http.post(Global.url+'/editRoleService', this.deleteobj)
    .subscribe(data => {
      var roleData = data.json();
      console.log("roleData"+JSON.stringify(roleData))
      this.toastr.success('Service Categories Updated Successfuly...');
      this.router.navigate(['/auth/roles'])
    }, error => {
      this.toastr.warning('Unable to Connect to Server')// Error getting the data
    });
  }
  onCloseHandled(){
    this.displayConfirmation='none'
  }
  cancel(){
    this.router.navigate(['/auth/roles'])
  }
  home(){
  this.router.navigate(['/admin/dashboard'])
}
}
