import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, RouteConfigLoadEnd, RouteConfigLoadStart, RouterEvent, NavigationCancel } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import { AuthService } from '../../auth/auth.service'
import { NgxSpinnerService } from 'ngx-spinner';
@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss']
})
export class ForgotPasswordComponent implements OnInit {
  userData: any;
  displayRoles = [];
  forgotPassword: FormGroup;
  userDataStatus: boolean = false;
  displayCredential: boolean = false;
  displayingRoles: boolean = false;
  constructor(
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public toastr: ToastrService,
    private http: Http,
    public authService: AuthService,
    private spinner: NgxSpinnerService,
  ) { }

  ngOnInit() {
    this.forgotPassword = this.formBuilder.group({
      modeOfCommunication: ['', Validators.required],
      role: ['', Validators.required],
    })
    this.getUserInfo(this.activatedRoute.snapshot.params['emailId'])
  }

  selectRole() {
    if (this.displayRoles.length > 0) {
      var userData = this.displayRoles.filter(val => val.role == this.forgotPassword.value.role)
      this.userData = userData[0]
      this.userData.maskedMobileNumber = this.userData.mobileNumber.replace(/.(?=.{4})/g, 'x');
      this.userData.maskedMobileNumber = this.userData.maskedMobileNumber.substr(0,3)+'-'+this.userData.maskedMobileNumber.substr(3,3)+'-'+this.userData.maskedMobileNumber.substr(8,8)
      this.displayCredential = true;
    }
  }

  verifier() {
    if (this.forgotPassword.invalid) return;
    this.passCodeGenerator(this.forgotPassword.value.modeOfCommunication)
  }

  cancel() {
    this.router.navigate(['/auth/login/0']);
  }

  passCodeGenerator(mode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    let passKeyObj = {
      "mode": mode,
      "role": this.userData.role,
      "source": mode == 'sms' ? this.userData.mobileNumber : this.userData.emailId,
      "emailId": this.userData.emailId
    }
    this.http.post(Global.url + '/passcodeGeneratorForPasswordGeneration', passKeyObj, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.router.navigate(['/auth/forgotPassOtpVerification/' + mode + '/' + this.userData.emailId + '/' + this.userData.role]);
        }
      })
  }
  getUserInfo(emailId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getUserInfo/' + emailId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.userData = response.response[0]
          this.displayRoles = response.response
          this.displayRoles.forEach((e) => {
            e.maskedMobileNumber = e.mobileNumber.replace(/.(?=.{4})/g, 'x')
            e.maskedMobileNumber = e.maskedMobileNumber.substr(0,3)+'-'+e.maskedMobileNumber.substr(3,3)+'-'+e.maskedMobileNumber.substr(8,8)
          })
          this.forgotPassword.get('role').setValue(this.userData.role)
          this.selectRole()
          this.displayRoles.length == 1 ? this.displayingRoles = false : this.displayingRoles = true
          this.userDataStatus = true
        }
      })
  }
}
