import { Component, OnInit, ViewChild } from '@angular/core';
import { Beyond } from '../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global'

@Component({
  providers: [Beyond],
  selector: 'app-list-roles',
  templateUrl: './list-roles.component.html',
  styleUrls: ['./list-roles.component.scss']
})
export class ListRolesComponent implements OnInit {
  @ViewChild('myInput') myInput
  roleData: any;
  dataRoles: boolean = false;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.getRoles();
  }

  getRoles() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getRbacRolesData', options)
      .subscribe(data => {
        var roles = data.json();
        this.roleData = roles.response;
        this.roleData.forEach((e)=>{
          console.log(e)
          if(e.permissions === ""||e.permissions === null||e.permissions === undefined){
            e.roleStatus = "NO PERMISSION"
          }else{
            e.roleStatus = "PERMISSIONS"
          }
        })
        this.dataRoles = true;
        console.log("service category Data" + JSON.stringify(this.roleData))
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  addRole(){
    this.router.navigate(['/auth/addRole'])
  }

  editRole(roleCode){
    console.log("roleCode"+JSON.stringify(roleCode))
    this.router.navigate(['/auth/editRole', roleCode.roleCode])
  }

}
