import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, RouteConfigLoadEnd, RouteConfigLoadStart, RouterEvent, NavigationCancel } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import { AuthService } from '../../auth/auth.service'
import { NgxSpinnerService } from 'ngx-spinner';
@Component({
  selector: 'app-forgot-password-email-validation',
  templateUrl: './forgot-password-email-validation.component.html',
  styleUrls: ['./forgot-password-email-validation.component.scss']
})
export class ForgotPasswordEmailValidationComponent implements OnInit {
  loginData: any;
  proData: any;
  mailOTP: any;
  forgotPassword: FormGroup;
  submitted: boolean = false
  mailError: boolean = false;
  constructor(
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public toastr: ToastrService,
    private http: Http,
    public authService: AuthService,
    private spinner: NgxSpinnerService,
  ) { }

  ngOnInit() {
    this.forgotPassword = this.formBuilder.group({
      emailId: ['', Validators.required],
    })
  }

  get f() { return this.forgotPassword.controls; }


  userNameVerification() {
    return new Promise(async (resolve, reject) => {
      if (this.forgotPassword.value.emailId == "") {
        this.mailError = false;
        return false;
      } else {
        this.http
          .get(
            Global.url +
            "/newUserNameVerification/" +
            this.forgotPassword.value.emailId
          )
          .subscribe(async data => {
            var mailResponse = data.json();
            if (mailResponse.status == "TRUE") {
              this.mailError = false;
              resolve({
                status: 'TRUE',
              })
            } else {
              this.mailError = true;
              resolve({
                status: 'FALSE',
              })
            }
          });
      }
    })
  }

  onSubmit() {
    this.submitted = true
    if (this.forgotPassword.invalid) return;
    this.router.navigate(['/auth/forgotPass', this.forgotPassword.value.emailId]);
  }

  cancel() {
    this.router.navigate(['/auth/login/0']);
  }

}
