import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, RouteConfigLoadEnd, RouteConfigLoadStart, RouterEvent, NavigationCancel } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import { AuthService } from '../../auth/auth.service'
import { NgxSpinnerService } from 'ngx-spinner';
import { CustomValidators } from "../../../utilities/customValidators";
@Component({
  providers: [CustomValidators],
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  serProSignIn: FormGroup;
  response: any;
  prozCode: any;
  prozProfileResponce: any;
  roleCode: any;
  roleOptions: boolean = false;
  submitted: boolean = false;
  public mailError: boolean = false;
  public mailExists: boolean = false;
  disableUsername: boolean;
  passwordError: boolean;
  noVanlynkLogin: boolean = false;
  RoleDataValue: any;
  constructor(
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public toastr: ToastrService,
    private http: Http,
    public authService: AuthService,
    private spinner: NgxSpinnerService,
  ) {

    this.router.events.subscribe((event: RouterEvent): void => {
      if (event instanceof RouteConfigLoadStart) {
        //console.log('The load started ')
        this.spinner.show()
      } else if (event instanceof RouteConfigLoadEnd ||
        event instanceof NavigationCancel) {
        this.spinner.hide()
        //console.log('The load ended ')
      } else {
        //console.log('The else load')
        this.spinner.hide()
      }
    })


    this.serProSignIn = this.formBuilder.group({
      Email: ['', Validators.required],
      password: ['', Validators.required],
      role: ['']
    });
  }

  ngOnInit() {
    this.prozCode = localStorage.getItem("prozCode")
  }

  get f() {
    return this.serProSignIn.controls;
  }

  login() {
    let obj = {
      "emailId": this.serProSignIn.value.Email,
      "password": this.serProSignIn.value.password,
      "role": this.serProSignIn.value.role
    }
    console.log(obj)
    if (this.serProSignIn.value.role == 'CEO' ||this.serProSignIn.value.role == 'IT Manager' || this.serProSignIn.value.role === 'COO' || this.serProSignIn.value.role === 'CFO' || this.serProSignIn.value.role === 'Operation Manager' ||
      this.serProSignIn.value.role === 'STAFF' || this.serProSignIn.value.role === 'Field Service Tech' || this.serProSignIn.value.role === 'Dispatcher' || this.serProSignIn.value.role === 'Bookkeeper' || this.serProSignIn.value.role === 'Day Labor'
      || this.serProSignIn.value.role === 'Admin' || this.serProSignIn.value.role === 'Owner' || this.serProSignIn.value.role === 'President' || this.serProSignIn.value.role === 'Channel Manager') {
      this.authService.staffLogin(obj)
        .subscribe(data => {
          var loginData = data
          if (loginData.status == 200) {
            this.spinner.show();
            this.response = data.data
            localStorage.setItem('login', JSON.stringify(this.response))
            this.router.navigate(['/staff/staffDashboard'])
          } else {
            this.passwordError = true

          }
        }, error => {
          this.toastr.warning('Unable to connect to server');
        })
    }
    else {
      this.authService.login(obj)
        .subscribe(data => {
          var loginData = data
          if (loginData.status == 200) {
            this.spinner.show();
            this.response = data.data
            localStorage.setItem('login', JSON.stringify(this.response))
            if (data.data.role === 'PRO') {
              this.prozProfileExistence(data.data.loginCode)
            } else if (data.data.role === 'CUST' && data.data.vanlynk != 'Y') {
              if (this.activatedRoute.snapshot.params['id'] == 1) {
                this.router.navigate(['/customer/selectProz', data.data.loginCode])
              } else if (this.activatedRoute.snapshot.params['id'] == 0) {
                this.router.navigate(['/customer/customerDashboard', data.data.loginCode])
              }
            } else if (data.data.role === 'CUST' && data.data.vanlynk === 'Y') {
              this.spinner.hide();
              this.noVanlynkLogin = true;
            } else if (data.data.role === 'ADMIN') {
              this.router.navigate(['/admin/dashboard'])
            } else if (data.data.role === 'VANLYNK') {
              this.router.navigate(['/proz-dashboard/prozDashboard', data.data.loginCode])
            }
          } else {
            this.passwordError = true
          }
        }, error => {
          this.toastr.warning('Unable to connect to server');
        })
    }

  }


  async onSubmit() {
    this.submitted = true;
    if (this.serProSignIn.value.Email == "") {
      return;
    } else if (this.serProSignIn.value.Password == "") {
      return;
    } else {
      var existsOrNot = await this.userNameVerification()
      console.log(existsOrNot)
      if (existsOrNot['status'] == "FALSE") {
        this.mailError = true
      } else if (existsOrNot['status'] == "TRUE") {
        this.mailError = false
        this.passwordError = false
        this.login();
      }
    }
  }

  dirtRemover() {
    this.mailError = false
    this.passwordError = false
    this.roleCheckingMail()
  }
  dirtRemoverForPassword() {
    this.mailError = false
    this.passwordError = false
  }


  prozProfileExistence(loginCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/prozProfileCount' + "/" + loginCode, options)
      .subscribe(data => {
        var response = data.json();
        this.prozProfileResponce = response.response;
        if (this.prozProfileResponce == 1) {
          this.router.navigate(['/proz-dashboard/prozDashboard', loginCode])
        } else {
          this.router.navigate(['/prozProfile/prozProfile', loginCode])
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }

  userNameVerification() {
    return new Promise(async (resolve, reject) => {
      if (this.serProSignIn.value.Email == "") {
        this.mailError = false;
        this.disableUsername = false;
        return false;
      } else {
        this.http
          .get(
            Global.url +
            "/newUserNameVerification/" +
            this.serProSignIn.value.Email
          )
          .subscribe(async data => {
            var mailResponse = data.json();
            if (mailResponse.status == "TRUE") {
              this.mailError = false;
              this.passwordError = false;
              resolve({
                status: 'TRUE',
              })
            } else {
              this.mailError = true;
              resolve({
                status: 'FALSE',
              })
            }
          });
      }
    })
  }






  roleCheckingMail() {
    return new Promise((resolve, reject) => {
      if (this.serProSignIn.value.Email == "") {
        this.mailError = true;
        this.passwordError = true;
        this.disableUsername = false;
        return false;
      } else {
        this.http.get(Global.url + '/roleCheckingWithEmail/' + this.serProSignIn.value.Email)
          .subscribe(data => {
            var roleCheckingResponse = data.json();
            if (roleCheckingResponse.status == 200) {
              if (roleCheckingResponse.response.length > 1) {
                this.roleOptions = true;
                this.RoleDataValue = roleCheckingResponse.response
              } else if (roleCheckingResponse.response.length == 1) {
                this.roleOptions = false;
                this.serProSignIn.get('role').setValue(roleCheckingResponse.response[0].role)
              }

            } else {
              this.mailError = false;
              this.passwordError = false;
            }
          }
          )
      }
    })
  }

  async forgotPassword() {
    if (this.serProSignIn.value.Email == "") {
      this.toastr.success('Enter your Email ID to reset your password')
      return false;
    }
    var existsOrNot = await this.userNameVerification()
    console.log(existsOrNot)
    if (existsOrNot['status'] == "FALSE") {
      this.mailError = true
    } else if (existsOrNot['status'] == "TRUE") {
      this.mailError = false
      this.router.navigate(['/auth/forgotPass', this.serProSignIn.value.Email]);
    }
  }

}


