import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({
  providers:[Beyond],
  selector: 'app-add-role',
  templateUrl: './add-role.component.html',
  styleUrls: ['./add-role.component.scss']
})
export class AddRoleComponent implements OnInit {
  addRole: FormGroup; 
  constructor(
    public beyond:Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.addRole = this.formBuilder.group({
      roleCode: ['', Validators.required],
      roleName: ['', Validators.required],
    })
  }

  submit() {
    let obj = {
      "roleCode":this.addRole.value.roleCode,
      "roleName":this.addRole.value.roleName,
       "status": "ACTIVE"
    }
    this.http.post(Global.url + '/addRbacRoles', obj)
      .subscribe(data => {
        var rolesData = data.json();
        console.log("rolesData"+JSON.stringify(rolesData))
          this.toastr.success('Role Added Successfuly...');
          this.router.navigate(['/auth/roles'])
      }, error => {
        console.log(error);// Error getting the data
      });
  }
  cancel(){
    this.router.navigate(['/auth/roles'])
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
}
