import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, RouteConfigLoadEnd, RouteConfigLoadStart, RouterEvent, NavigationCancel } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import { AuthService } from '../../auth/auth.service'
import { NgxSpinnerService } from 'ngx-spinner';
@Component({
  selector: 'app-forgot-password-otp-verification',
  templateUrl: './forgot-password-otp-verification.component.html',
  styleUrls: ['./forgot-password-otp-verification.component.scss']
})
export class ForgotPasswordOtpVerificationComponent implements OnInit {
  forgotPasswordOtpVerification: FormGroup;
  submitted: boolean = false
  otpValidated: boolean = false
  otpNotValidated: boolean = false
  oneToManyRoles = []
  userData :any;
  constructor(
    public router: Router,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    public toastr: ToastrService,
    private http: Http,
    public authService: AuthService,
    private spinner: NgxSpinnerService,
  ) {
    this.forgotPasswordOtpVerification = this.formBuilder.group({
      otp: ['', Validators.required]
    });
  }
  get g() { return this.forgotPasswordOtpVerification.controls; }

  ngOnInit() {
    this.getUserInfo(this.route.snapshot.params['source'])
  }

  vcodeChange(vcode) {
    if (vcode == undefined || vcode == '' || vcode == null) return
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/validateverificationcode/' + this.route.snapshot.params['source'] + '/' + this.route.snapshot.params['mode'] + '/' + vcode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.otpValidated = true
          this.otpNotValidated = false
        } else {
          this.otpValidated = false
          this.otpNotValidated = true
        }
      })
  }

  resendVerificationCode() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    let passkeyObj = {
      "mode": this.route.snapshot.params['mode'],
      "emailId": this.route.snapshot.params['source'],
      "role": this.route.snapshot.params['role'],
      "source":this.route.snapshot.params['mode'] == "sms" ? this.userData.mobileNumber : this.userData.emailId
    }
    this.http.post(Global.url + '/passcodeGeneratorForPasswordGeneration', passkeyObj, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.route.snapshot.params['mode'] == 'sms' ? this.toastr.success('You will received your verification code to your mobile shortly') : this.toastr.success('You will received your verification code to your email shortly')
        }
      })
  }


  verifyOTP() {
    this.submitted = true
    if (this.forgotPasswordOtpVerification.invalid || this.otpValidated == false) {
      return;
    }
    this.router.navigate(['/auth/forgotPassNewPwdConfirmation/' + this.route.snapshot.params['mode'] + '/' + this.route.snapshot.params['source']+'/'+this.route.snapshot.params['role']]);
  }

  getUserInfo(emailId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getUserInfo/' + emailId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.oneToManyRoles = response.response
          var userData = this.oneToManyRoles.filter(val=>val.emailId == emailId && val.role == this.route.snapshot.params['role'])
          this.userData = userData[0]
        }
      })
  }
}
