import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, RouteConfigLoadEnd, RouteConfigLoadStart, RouterEvent, NavigationCancel } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import { AuthService } from '../../auth/auth.service'
import { NgxSpinnerService } from 'ngx-spinner';
import { RegistrationValidator } from '../../../utilities/validator';
@Component({
  selector: 'app-forgot-password-new-pwd-confirmation',
  templateUrl: './forgot-password-new-pwd-confirmation.component.html',
  styleUrls: ['./forgot-password-new-pwd-confirmation.component.scss']
})
export class ForgotPasswordNewPwdConfirmationComponent implements OnInit {
  passwordFormGroup: FormGroup;
  submitted: boolean;

  constructor(
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public toastr: ToastrService,
    private http: Http,
    public authService: AuthService,
    private spinner: NgxSpinnerService,
  ) {
    this.passwordFormGroup = this.formBuilder.group({
      password: ['', Validators.required],
      repeatPassword: ['', Validators.required]
    }, {
      validator: RegistrationValidator.validate.bind(this)
    });
  }


  get f() { return this.passwordFormGroup.controls; }

  ngOnInit() {
  }

  changePassword() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.passwordFormGroup.invalid) {
      return;
    }
    this.spinner.show()
    let newPasswordObj = {
      emailId: this.activatedRoute.snapshot.params['source'],
      password: this.passwordFormGroup.value.repeatPassword,
      role: this.activatedRoute.snapshot.params['role'],
    }
    this.http.post(Global.url + '/createNewPassword', newPasswordObj)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.spinner.hide()
          this.toastr.success('Password Changed Successfully')
          this.router.navigate(['/auth/login/0']);
        }
      })
  }

  cancel() {
    this.router.navigate(['/auth/login/0']);
  }
}
