import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global'

@Component({
  providers: [Beyond],
  selector: 'app-role-matrix',
  templateUrl: './role-matrix.component.html',
  styleUrls: ['./role-matrix.component.scss']
})
export class RoleMatrixComponent implements OnInit {
  roleName: any;
  public modulesData = [];
  public terminateCollapsableUtility = [];
  public tempAccessLevel = [];
  public roleDataPermissions = [];
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    public route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.roleName = this.route.snapshot.params['string']
    this.fetchPermissions(this.route.snapshot.params['anyCode'])
    this.moduleLoader()
  }
  //PERMISSION FETCHER
  fetchPermissions(roleCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewRoles/' + roleCode, options)
      .subscribe(data => {
        var permissions = data.json();
        if (permissions.status == 200) {
          this.tempAccessLevel = permissions.response[0].permissions.split(',').map(function (i) {
            return parseInt(i, 10);
          })
          return false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  //PERMISSION FETCHER
  //MODULE LOADER
  moduleLoader() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getAccessPermissions', options)
      .subscribe(data => {
        var module = data.json();
        if (module.status == 200) {
          this.modulesData = module.response
          this.modulesData.forEach((item, index) => {
            item.checked = false;
            item.openCollapse = true;
            item.closeCollapse = false;
          });
          this.modulesData.forEach((item) => this.tempAccessLevel.forEach((permissions) => {
            if (item.id == permissions) {
              item.checked = true;
              item.openCollapse = true;
              item.closeCollapse = false;
            }
          }))
          return false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  //MODULE LOADER
  //COLLAPSE OPEN
  openCollapse(modulesData) {
    this.terminateCollapsableUtility.push(modulesData)
    if (this.terminateCollapsableUtility.length == 2) {
      this.terminateCollapsableUtility[this.terminateCollapsableUtility.length - 2].openCollapse = true;
      this.terminateCollapsableUtility[this.terminateCollapsableUtility.length - 2].closeCollapse = false;
      this.terminateCollapsableUtility.shift()
    }
    modulesData.openCollapse = false;
    modulesData.closeCollapse = true;
    modulesData.mainId = modulesData.id
  }
  //COLLAPSE OPEN
  //CLOSE COLLAPSE
  closeCollapse(modulesData) {
    modulesData.openCollapse = true
    modulesData.closeCollapse = false
  }
  //CLOSE COLLAPSE 
  //MASTER MODULE CHANGE DETECTION
  changeMasterModule(masterModule) {
    this.tempAccessLevel.push(masterModule.id)
    this.modulesData.forEach((item, index) => {
      if (item.parentId == masterModule.id && item.checked == true) {
        item.checked = masterModule.checked;
        this.tempAccessLevel = this.tempAccessLevel.filter(value => value !== item.id && value !== masterModule.id);
      } else if (item.parentId == masterModule.id && item.checked == false) {
        item.checked = masterModule.checked;
        this.tempAccessLevel.push(item.id)
      }
    });
  }
  //MASTER MODULE CHANGE DETECTION
  //JUNIOR MODULE CHANGE DETECTION
  changeJuniorModule(juniorModule, id, checked) {
    if (juniorModule.checked == true) {
      this.permissionExist(juniorModule, id);
    } else {
      //this.tempAccessLevel = this.tempAccessLevel.filter(value => value !== juniorModule.id);
      this.removePermission(juniorModule, id, checked)
    }
  }
  //JUNIOR MODULE CHANGE DETECTION
  //PERMISSION EXIST
  permissionExist(juniorModule, id) {
    if (this.tempAccessLevel.includes(id) == true) {
      this.tempAccessLevel.push(juniorModule.id)
      console.log('The temp if' + JSON.stringify(this.tempAccessLevel))
    } else {
      this.tempAccessLevel.push(id)
      this.modulesData.forEach((item, index) => {
        if (item.id == id) {
          item.checked = juniorModule.checked
          this.tempAccessLevel.push(juniorModule.id)
        }
      })
      console.log('The temp else' + JSON.stringify(this.tempAccessLevel))
    }
  }
  //PERMISSION EXIST
  //REMOVE PERMISSION
  removePermission(juniorModule, id, checkedModule) {
    if (this.tempAccessLevel.includes(id) == true) {
      if (this.tempAccessLevel.length == 2) {
        this.tempAccessLevel = this.tempAccessLevel.filter(value => value !== juniorModule.id && value !== id);
        console.log('The temp removePermission' + JSON.stringify(this.tempAccessLevel))
        this.modulesData.forEach((item, index) => {
          if (item.id == id) {
            item.checked = false;
          }
        })
        return false;
      }
      this.tempAccessLevel = this.tempAccessLevel.filter(value => value !== juniorModule.id);
      console.log('The temp removePermission' + JSON.stringify(this.tempAccessLevel))
    }
  }
  //REMOVE PERMISSION
  //GRANT PERMISSION
  grantPermission() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    var permissionsObj = {
      permissions: this.tempAccessLevel,
      roleCode: this.route.snapshot.params['anyCode']
    }
    this.http.post(Global.url + '/grantPermissions', permissionsObj, options)
      .subscribe(data => {
        var accessPermission = data.json();
        if (accessPermission.status == 200) {
          this.fetchPermissions(this.route.snapshot.params['anyCode'])
          this.toastr.success('Permissions Granted Successfully')
          return false;
        } else {
          this.toastr.warning('Permission Granting Unsuccessful')
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  //GRANT PERMISSION
  //SELECT ALL
  selectAll(selectAllPermissions) {
    this.modulesData.forEach((e) => {
      e.checked = selectAllPermissions
    })
  }
  //SELECT ALL
  home(){
    this.router.navigate(['/admin/dashboard'])
  }
}
